.db-login-container {
  box-shadow: 0 30px 50px -50px white;
  padding: 1em;
  margin: 2em;
}

.db-login-container h4 {
  margin: 0 auto 0.5em;
  padding: 0;
  font: var(--spt-font-subheading);
}

.db-login-container,
.button-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

.button-container > * {
  max-width: 25rem;
  width: 100%;
  flex-grow: 1;
}

.button-container button,
.button-container span {
  font-size: 75%;
}

.button-container button {
  border-radius: 50px;
}
.db-login-container p {
  margin: 0 auto;
  padding: 0;
  width: 100%;
  max-width: 39em;
  width: 75%;
}

.db-login-container button {
  margin-top: 1em;
}
.db-login-status {
  margin: 1em;
}

.db-login-status {
  margin: auto;
}

.login-text {
  display: inline-block;
  margin: 0 auto;
  padding: 0.25em 2em;
  font-weight: 700;
  width: 100%;

  /* border: 1px inset var(--spt-color-background); */
  background-color: var(--spt-color-background);
  color: var(--spt-color-accent-2);
  border-radius: 50px;
  text-transform: uppercase;
}

.not-logged-in-text {
  background: var(--spt-color-accent-2);
  color: var(--spt-color-background);
  -webkit-text-fill-color: initial;
  font-size: 1em;
  font-size: calc(0.7em + 0.25vw);
  font-weight: 700;
  /* border: 2px inset var(--spt-color-accent); */
  box-shadow:
    inset 1px 1px 3px -2px var(--spt-color-background),
    inset -1px -1px 3px -2px white,
    2px 2px 3px -2px var(--spt-color-accent-2);
  padding: 0.5em 1em;
}

@media (min-width: 1400px) {
  .not-logged-in-text {
    font-size: 1em;
  }
}

.form-container {
  // box-shadow: 3px 3px 10px -7px inset black, -3px -3px 10px -7px inset white,
  //   2px 2px 3px -2px #ffffff99;
  box-shadow: none;
  width: 100%;
  max-width: none;
  border-radius: 50px;
  margin: 0px auto;
}

body .form-container label {
  font-size: 1em;
}

body .form-container input[type="text"],
body .form-container input[type="email"],
body .form-container input[type="password"] {
  font-size: 1em;
  padding: 0px 2em;
  margin: 0;
  height: 2em;
  box-shadow:
    inset 2px 2px 3px -2px rgba(0, 0, 0, 0.5),
    inset -2px -2px 5px -2px white,
    2px 2px 5px -2px white;
}

.button-container button {
  margin: 0.25em auto 0.4em;
  padding: 0.5em 2em;
  width: 100%;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  /* background: var(--spt-color-background-gradient); */
  color: var(--spt-color-accent-2);
  box-shadow:
    inset 1px 1px 1px rgb(255 255 255 / 25%),
    inset 1px -1px 1px rgb(0 0 0 / 15%),
    1px 1px 5px -3px black;
  box-shadow:
    3px 3px 7px -5px white inset,
    -3px -3px 7px -5px rgba(0, 0, 0, 0.5) inset;
  border: none;
  font: var(--spt-font-body-text);
  font-size: 1em;
  font-weight: 700;
}

.button-container button:hover {
  background: var(--spt-color-accent-2);
  color: var(--spt-color-background);
  transform: none;
}
/* HORIZONTAL DISPLAY */
.db-login-container.horizontal-display {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  padding: 0;
  max-width: max-content;
  font: inherit;
  max-height: 100%;
  height: fit-content;
}
.db-login-container.horizontal-display * {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  padding: 0;
  max-width: max-content;
  font: inherit;
  /* height: 30px; */
  min-height: fit-content;
  max-width: 100%;
}

.db-login-container.horizontal-display form input,
.db-login-container.horizontal-display form button {
  min-height: 30px;
  height: 100%;
  max-height: none;
}

.db-login-container.horizontal-display span {
  display: flex;
  align-items: center;
}

.db-login-container.horizontal-display span * {
  min-height: 30px;
}

.db-login-container.horizontal-display span button {
  margin-left: 1em;
  color: var(--spt-color-accent-2);
  text-shadow: 0 0 3px rgb(245, 222, 179);
  background: linear-gradient(rgb(255 135 0) 37%, rgba(0, 0, 0, 1) 100%);
}
.db-login-container.horizontal-display label {
  display: none;
}
.db-login-container.horizontal-display input::placeholder {
  color: #0000005e;
  opacity: 1;
  font-weight: 700;
}
.db-login-container.horizontal-display input {
  border: 1px inset #c8c8c8;
}
.db-login-container.horizontal-display button {
  margin: 0 1em 0 0;
  padding: 0 1em;
}

.db-login-container.horizontal-display p {
  margin: 0;
}
.db-login-container.horizontal-display .not-logged-in-text,
.db-login-container.horizontal-display h4,
.db-login-container.horizontal-display h3 {
  display: none;
}

.db-login-container.horizontal-display .button-container,
.db-login-container.horizontal-display .form-container {
  margin: 0 auto;
  padding: 0;
  box-shadow: none;
}

.db-login-container.horizontal-display .not-logged-in-text {
  display: none;
}

.db-login-container.horizontal-display .login-question {
  margin-right: 3em;
}

.login-question button {
  font-variant: small-caps;
  border: 7px solid red;
}

.db-login-container.horizontal-display .login-text {
  padding: 0 1em;
  background: none;
  color: inherit;
  border: none;
  box-shadow: none;
  align-items: center;
  max-width: max-content;
  min-width: 10em;
}

.demo-cta-wrap {
  position: relative;
  padding: 0.25em 1.25em;
  font-size: 1.6rem;
  background: var(--spt-color-accent-dark);
  color: var(--spt-color-background);
  border-radius: 50px 50px;
  overflow: hidden;
  transition: 0.3s all ease;
  text-decoration: none;
  border: none;
  margin: 1em auto;
}

.demo-cta-wrap:hover {
  box-shadow: 0 0 10px 0 yellow;
}

.demo-cta-wrap > div {
  padding: 0;
  font-size: 1.6rem;
  background: inherit;
  color: inherit;
  box-shadow: none;
}

.logo-to-outerwrap-header a.demo-cta-wrap {
  transform: scale(1) translateY(100%);
}
