[data-forreview="true"] {
  position: relative;
}

[data-forreview="true"]:before {
  content: "";
  background-color: rgb(85 85 85 / 68%);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  pointer-events: none;
}

[data-forreview="true"]:after {
  content: "Review Needed";
  background: var(--spt-color-accent-3);
  color: var(--spt-color-background-warm);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -5%);
  width: -moz-fit-content;
  width: fit-content;
  min-width: 11em;
  text-align: center;
  font-size: 0.9rem;
  padding: 0 2em;
  letter-spacing: 0.5em;
  border-radius: 25px;
  font-weight: 500;
}

ul ul[type="hold"]:after {
  content: "On Hold";
  background: var(--spt-color-background-contrast);
  color: var(--spt-color-background);
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -5%);
  width: -moz-fit-content;
  width: fit-content;
  min-width: 11em;
  text-align: center;
  font-size: 0.9rem;
  padding: 0 2em;
  letter-spacing: 0.5em;
  border-radius: 25px;
  font-weight: 500;
}

@media screen and (max-width: 900px) {
  .master-parent-group > {
    div > {
      li {
        min-width: 0;
      }
      li[id*="progressbar-item"] {
        min-width: fit-content;
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .master-parent-group {
    border-radius: 12.5px;
    margin: 2vh auto;
    width: 100%;

    > button {
      max-height: 3em !important;
    }
  }
  ul {
    ul[data-markedcomplete="true"]:after,
    ul[data-forreview="true"]:after,
    ul[type="hold"]:after {
      font-size: calc(0.2rem + 1vmin);
    }
  }

  div[class*="pseudo-collapsible-elm"] {
    li[class*="method"],
    li[class*="priority"] {
      display: none;
    }
  }
}

